/* =============================
==========footer============= */
/* Original Styles */
.footer {
  color: white;
  padding: 25px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #434343, #000000);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .footer {
    text-align: center;
  }

  .footer h2 {
    margin-top: 20px;
  }

  .footer .row {
    flex-direction: column;
  }

  .footer form {
    margin-top: 20px;
  }

  .footer hr {
    margin-top: 20px;
  }

  .footer p {
    margin-bottom: 0;
  }
}


/* ========================================= */
/* =========contact us ========= */
/* Original Styles */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .contactus {
    flex-direction: column;
    text-align: center;
  }

  .col-md-6,
  .col-md-4 {
    width: 100%;
  }

  .col-md-4 {
    margin-top: 20px;
  }

  .col-md-4 img {
    width: 80%; /* Adjust the width of the image for better responsiveness */
    margin: 0 auto; /* Center the image */
  }

  .col-md-4 h1 {
    font-size: 1.5em; /* Adjust the font size for better readability on smaller screens */
  }

  .col-md-4 p {
    margin: 10px 0;
  }

  .form-group label,
  .form-group input,
  .form-group textarea,
  .btn.btn-primary {
    width: 100%;
    box-sizing: border-box;
  }

  .social-icons {
    justify-content: center;
  }

  .social-icons a {
    margin: 0 10px;
  }

  .mt-3 {
    margin-top: 10px;
  }
}


