/* Shared styles */
.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
  background-color: #ffdee9;
  background-image: linear-gradient(0deg, #ffdee9 0%, #b5fffc 100%);
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
}

.form-container form input {
  border: none;
  border-bottom: 1px solid #000;
  border-radius: 0;
}

.form-container form button {
  border: 1px solid black;
  border-radius: 0;
  background-color: #000;
  color: white;
  width: 250px;
}

.form-container form button:hover {
  border: 1px solid black;
  background: #000000;
  background: -webkit-linear-gradient(to right, #434343, #000000);
  background: linear-gradient(to right, #434343, #000000);
}

.forgot-btn {
  background-color: #434343a4 !important;
  border: 0 !important;
}

.forgot-btn:hover {
  color: white !important;
}

/* Additional styles for icons and show/hide password */
.icon {
  font-size: 20px;
  margin-right: 10px;
  color: #007bff;
}

.toggle-password-icon {
  cursor: pointer;
  color: #007bff;
}

.toggle-password-icon:hover {
  color: #0056b3;
}

.form-container form .password-input {
  display: flex;
  align-items: center;
}

.form-container form .password-input input {
  border: none;
  border-bottom: 1px solid #000;
  border-radius: 0;
  flex-grow: 1;
}

.form-container form .toggle-password {
  margin-left: 10px;
  display: flex;
  align-items: center;
}

.form-container form .toggle-password input {
  margin: 0;
}

.form-container form .toggle-password span {
  margin-left: 5px;
}

.form-container form button {
  border: 1px solid #007bff;
  border-radius: 10px;
  background-color: #007bff;
  color: white;
  width: 250px;
}

.form-container form button:hover {
  border: 1px solid #0056b3;
  background: #007bff;
}
